//vuex
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)

const state={
	count:2,
	sss:true,
	guanzhu:[],
	dandingquan_on:true,
	carno:[],
	man_Clothes:[],
	userid:'',
	cheackin:false,
	info_sendincar:'',
	transitionName:'',
	article_show:false,
	//info渲染数据暂存
	infozancun:'',
	userName:'',
	password:''
}


const mutations={
	add(state,num,name){
		state.count+=num;
	},
	bind(state,tt){
		state.sss=tt;
	}
}

export default new Vuex.Store({
	state,
	mutations
})



