<template>
	<div>
		<scroller  refreshText="你好">
		<!--顶部搜索框-->
		<div class="top_head">
			<i class="iconfont icon-sousuo" style="text-align: center;background: transparent;font-size: 20px;"></i>
			<img src="../assets/1548341655.jpg"/>
			<i class="iconfont icon-shouhoukefu" style="text-align: center;background: transparent;font-size: 20px;"></i>
		</div>
		<div class="top_nav">
			<ul>
				<li @click="pinpai(item.Id)"  v-for="(item,index) in title" :key='index' :pid=item.Id>
					<p>{{item.Name}}</p>
				</li>
			</ul>
		</div>
		<div class="classification">
			<img :src="top_imp"/>
		</div>
		<!--商标轮播-->
		<div class="swiper_box" >
			<div class="brand">
				<span>品牌馆 | <span class="brandsp">Brand Pavilion</span></span>
				<span>MORE &gt;</span>
			</div>
			<swiper :options="swiperOption">
				<!--v-for='(item,index) in imp'-->
				<swiper-slide v-for='(item,index) in imp' :key="index" :pid=item.Id >
					<img @click="send(item.Id)" :src="item.brandImage"/>
				</swiper-slide>
			</swiper>
			<div class="brand_bottom">
				
			</div>
			<!--以下看需要添加<-->
			<div class="swiper-scrollbar"></div>
			<div class="swiper-pagination"></div>
			<!--//标页码-->
		</div>
		<div class="choose">
			<span>达人玩潮<span class="choosesp">| lcon Choose</span></span>
		</div>
		<!--商品列表-->
		
		<div id="lcon_list">
			<ul class="lcon_li">
				<li v-for="(total,indexe) in datalis_list" :key='indexe' :info_id='total.Id' @click="send_info(total.Id)">
					<img :src="total.ProductPic"/>
					<p>{{total.SalesName}}</p>
					<div>
						<p>￥{{total.ShowPrice}}</p>
						<p>￥{{total.marketPice}}</p>
					</div>
				</li>
			</ul>
		</div>
		</scroller>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				transitionName:'',
				swiperOption: {
//					slidesPerView : 3,
					centeredSlides : false,
					slidesPerView: 5,//一行显示3个
    				slidesPerColumn: 2,//显示2行
//  				spaceBetween : 5,
					on: {
					    slideChange: function(aa){
					    	console.log(this.activeIndex)
					    	if(this.activeIndex==14){
								location.href="#/message/quanzi";
					    	}
					    },
					}
					
				},
				imp:'',
				top_imp:'',
				title:'',
				datalis_list:'',
				go_pinpai:true
			}
		},
		mounted(){
			this.$store.state.cheackin=false
			console.log(this.go_pinpai)
			this.$axios({
				method: "get",
				url: "./static/biaoti.json",
				dataType: "json",
				crossDomain: true,
				cache: false
			}).then(res => {
				console.log(res)
				this.imp=res.data.logo.Info
				console.log(this.imp)
				this.top_imp=res.data.tory.Info[0].GetMobileHomeMainActiveList[0].Path
				this.title=res.data.Info_biaoti
				this.datalis_list=res.data.home.Info
				console.log(this.datalis_list)
			})
		},
		methods:{
			pinpai(id){
				console.log(id)
			},
			send(Id){
				console.log(Id)
				this.$router.push({name:'brand',query:{Id}})
			},
			send_info(info_id){
				console.log(info_id)
				this.$router.push({name:'info',params:{info_id}})
				this.$store.state.infozancun=info_id
				console.log(this.$store.state.infozancun)
			}
		},
	}
</script>

<style lang="less">
#lcon_list{
	z-index: 10;
	width: 100%;
	margin-top: 20px;
	.lcon_li{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		width: 100%;
		li{
			width: 350px;
			height: 540px;
			img{
				width: 350px;
				height: 350px;
			}
			p{
				width: 350px;
				height: 70px;
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 2;
				overflow: hidden;
				font-size: 25px;
			}
			div{
				width: 350px;
				height: 80px;
				line-height: 40px;
				p{
					width: 350px;
					height: 40px;
					&:nth-child(1){
						text-decoration: line-through;
						color: rgb(167,167,167);
					}
				}
			}
			
		}
	}
}
*{
	margin: 0;
	padding: 0;
}
.top_head{
	position: fixed;
	top: 0;
	background: white;
	z-index: 999;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 80px;
	/*background: red;*/
	i{
		width: 45px;
		height: 42px;
		background: yellow;
		margin: 0 20px 0 20px;
	}
	img{
		width:147px;
		height: 30px;
	}
}
.nav_Central{
	width: 100%;
	height: 306px;
	.nav_list{
		width: 100%;
		height: 306px;
	}
}
.brand{
	width: 100%;
	height: 70px;
	background: transparent;
	margin-bottom: 10px;
	background-color: rgba(245,245,245,0.5);
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: rgb(189,189,189);
	span{
		background: transparent;
		&:nth-child(1){
			color: #000;
			margin-left: 20px;
			background: transparent;
		}
		.brandsp{
			color: rgb(189,189,189);
			background: transparent;
		}
	}
}
.brand_bottom{
	width: 100%;
	height: 37px;
	/*background: blue;*/
	margin-top: 3px;
	background-color: rgba(245,245,245,0.5);
}
.swiper-slide img{
	width: 140px;
	height: 139px;
}
.swiper_box{
	width: 100%;
	height: 410px;
	background: url(../assets/thread_27828970156291_20190122043508_s_198013_o_w_800_h_752_77224.jpg);
}

.classification img{
	width: 100%;
	height: 535px;
}
.top_nav{
	width: 100%;
	height: 65px;
	background: white;
	position: fixed;
	z-index: 999;
	top: 80px;
	ul{
		display: flex;
		justify-content: space-around;
		align-items: center;
		width: 100%;
		height: 60px;
		li{
			color: black;	
		}
	}
}
.choose{
	width: 100%;
	height: 69px;
	background: rgb(255,255,255);
	display:flex;
	justify-content: flex-start;
	align-items: center;
	border-bottom: 1px solid rgb(219,219,219);
	span{
		&:nth-child(1){
			margin-left: 20px;
		}
		.choosesp{
			color:rgb(219,219,219);
		}
	}
}
</style>


