import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/home'
import Fenlei from '@/components/fenlei'
import Cart from '@/components/cart'
import My from '@/components/my'
import Message from '@/components/message'
import Brand from '@/components/brand'
import Login from '@/components/login'

import Info from '@/components/info/info'

import Fenleiinfo from '@/components/info/fenleiinfo'


import List from '@/components/fenlei/list'
import Pinpai from '@/components/fenlei/pinpai'

import Quanzi from '@/components/message/quanzi'
import Zhuboquan from '@/components/message/zhuboquan'

import Allinfo from '@/components/info/allinfo'
import Popularity from  '@/components/info/allinfo/Popularity'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      redirect:Home
    },
    {
    	path:'/login',
    	name:'login',
    	component:Login
    },
    {
    	path:'/home',
    	name:'home',
    	component:Home
    },
    {
    	path:'/fenlei',
    	name:'fenlei',
    	component:Fenlei,
    	children:[
	    		{
	    			path:"list",
	    			name:"list",
	    			component:List
	    		},
	    		{
	    			path:"pinpai",
	    			name:"pinpai",
	    			component:Pinpai
	    		}
    	]
    },
    {
    	path:'/cart',
    	name:'cart',
    	component:Cart
    },
    {
    	path:'/my',
    	name:'my',
    	component:My
    },
    {
    	path:'/message',
    	name:'message',
    	component:Message,
    	children:[
    		{
		    	path:"quanzi",
					name:"quanzi",
					component:Quanzi
		    },
		    {
		    	path:"zhuboquan",
					name:"zhuboquan",
					component:Zhuboquan
		    }
    	],
    	meta: {
    		isactive: '/message'
    	}
    },
//  {
//  	path:'/article',
//  	name:'article',
//  	component:Article,
//  },
    {
    	path:'/brand',
    	name:'brand',
    	component:Brand
    },
    //详情页
    {
    	path:'/info',
    	name:'info',
    	component:Info,
    	meta: {
    		isactive: '/info'
    	},
    },
    {
    	path:'/fenleiinfo',
    	name:'fenleiinfo',
    	component:Fenleiinfo,
    	meta: {
    		isactive: '/fenleiinfo'
    	}
    },
    {
    	path:'/allinfo',
    	name:'allinfo',
    	component:Allinfo,
    	children:[
	    		{
	    			path:"Popularity",
	    			name:"Popularity",
	    			component:Popularity
	    		}
    	]
    }
  ]
})



