							<span>{{item.name}}</span>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'list',
		data(){
			return{
				clothes:"",
				clothes_lady:'',
				box:'',
				box_lady:'',
				shoes:'',
				shoes_lady:'',
				manAccessories:'',
				manAccessories_lady:'',
			}
		},
		methods:{
			Allinfo(Info_id){
				console.log(Info_id)
				this.$router.push({name:'allinfo',params:{Info_id}})
				this.$store.state.count=Info_id
			},
		},
		mounted(){
			this.$axios({
				method:"get",
				url:"./static/fenlei.json",
				dataType:"json",
				crossDomain:true,
			}).then(res => {
				console.log(res)
				this.clothes=res.data.clothes
				this.clothes_lady=res.data.clothes_lady
				this.box=res.data.box
				this.box_lady=res.data.box_lady
				this.shoes=res.data.shoes
				this.shoes_lady=res.data.shoes_lady
				this.manAccessories=res.data.manAccessories
				this.manAccessories_lady=res.data.manAccessories_lady
			})
		}
		
	}
</script>

