<template>
		<div class="nav" v-if="$store.state.sss">
			<ul>
				
				<li v-for="(item,index) in navlist" :key='index' @click="dian(index)">	
					<router-link :to="item.url">
						<i :class="$route.path==item.url?item.icon:item.iconact"></i>
						<p>{{item.title}}</p>
					</router-link>
				</li>
			</ul>
		</div>
</template>

<script>
	export default{
		name:"navcom",
		data(){
			return{
				navlist:[
					{title:'首页',url:"/home",icon:"iconfont icon-shouye1",iconact:"iconfont icon-shouye",isact:false},
					{title:'分类',url:"/fenlei/list",icon:"iconfont icon-kinds ",iconact:"iconfont  icon-fenlei-",isact:false},
					{title:'主播间',url:"/message/quanzi",icon:"iconfont icon-zhuanshi",iconact:"iconfont icon-iconset0219",isact:false},
					{title:'购物车',url:"/cart",icon:"iconfont  icon-ziyuan",iconact:"iconfont icon-gouwuche",isact:false},
					{title:'我的',url:"/my",icon:"iconfont icon-wode",iconact:"iconfont icon-My",isact:false}
				]
				
			}
		},
		methods:{
			dian(index){
				for(var i=0;i<this.navlist.length;i++){
					this.navlist[i].isact=false;
				}
				this.navlist[index].isact=true;
			},
			
		},
		mounted(){
			console.log(this.$route.meta)
		}
		
	}
</script>

<style>
	.nav{
		z-index: 9999;
		position: fixed;
		left: 0;
		bottom: 0;
		background: white;
		width: 100%;
		height: 106px;
		/*background: red;*/
		box-shadow: -5px -0px 10px pink;
	}
	ul{
		list-style: none;
		padding: 0;
		margin: 0;
	}
	.nav ul{
		height: 106px;
		display: flex;
		justify-content: space-around;
		align-content: center;
	}
	.nav ul li{
		height: 106px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		text-align: center;
		z-index: 999;
	}
	.nav ul li a{
		text-decoration: none;
		
	}
	.nav ul li p{
		font-size: 15px;
		margin: 0;
		color: rgb(45,45,45);
	}
	.nav ul li i{
		background: white;
		font-size: 30px;
		color: rgb(45,45,45);
	}
	.nav ul li .router-link-active p{
		color: red;
	}
</style>


