<template>
	<div>
		<!--<h1>但丁圈</h1>-->
		<div class="circle">
			<img :src="imgs"/>
			<span>{{title}}</span>
			<i></i>
		</div>
		<div class="Recommend">
			<swiper :options="swiperOption">
				<!--v-for='(item,index) in imp'-->
				<swiper-slide v-for='(item,index) in list_Recommend' :key="index">
					<div class="swippp">
						<img :src="item.listimg"/>
						<span>{{item.listname}}</span>
						<span>￥{{item.price}}</span>
					</div>
					
				</swiper-slide>
			</swiper>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				swiperOption: {
					slidesPerView : 'auto',
					centeredSlides : false,
					slidesPerView: 3,//一行显示3个
    				slidesPerColumn: 1,//显示2行
    				spaceBetween : 5,
    				spaceBetween : '20%'
				},
				name:'qunzi',
				imgs:'',
				title:'',
				list_Recommend:''
			}
		},
		methods:{
			loder(){
				this.$axios({
					method:'get',
					url:"./static/showinfo.json",
					dataType:"json",
					crossDomain: true,
					cache: false
				}).then(res=>{
					console.log(res)
					this.ranking_anchor=res.data.off_white[0].anchor
					console.log(this.ranking_anchor)
					this.imgs=res.data.off_white[0].showimgs
					this.title=res.data.off_white[0].Title
					this.list_Recommend=res.data.off_white[0].list
					console.log(this.list_Recommend)
				})
			}
		},
		mounted(){
			this.loder()
		}
	}
</script>

<style lang="less">
	.circle{
		width: 100%;
		height: 750px;
		margin-top: 30px;
		position: relative;
		img{
			width: 100%;
			height: 750px;
		}
		span{
			width: 100%;
			position: absolute;
			bottom: 60px;
			background: none;
			color: white;
			font-size: 35px;
			overflow: hidden;
			text-overflow:ellipsis;
			white-space: nowrap;
		}
	}
	.Recommend{
		width: 100%;
		height: 410px;
		background: yellow;
		margin-top: 20px;
		margin-bottom: 150px;
		.swippp{
			width: 280px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			height: 410px;
			img{
				width: 280px;
				height: 280px;
				/*margin: 0 20px 0 20px;*/
			}
			span{
				/*margin: 0 20px 0 20px;*/
				
			}
			
			
		}
	}
</style>


