<template>
	<div>
		<!--<h1>123</h1>
		<h1>{{$route.params.Info_id}}</h1>-->
		<div class="alllist_nav">
			<ul>
				<li @click="changs()"><span>{{renqi}}</span></li>
				<li @click="changs_xiaoliang()"><span>{{xiaoliang}}</span></li>
				<li @click="changs_New()"><span>{{New}}</span></li>
				<li @click="changs_price()"><span>{{price}}</span><i style="background: white;" :class="iconfont"></i></li>
				<li @click="changs_shaixuan()"><span>{{shaixuan}}</span></li>
			</ul>
		</div>
		<div class="allinfo_list">
			<ul class="allinfo_box">
				<!-- <li v-for="(item,index) in $store.state.man_Clothes" :allinfo_id="item.Id"  @click="send_info(item.Id)"> -->
				<li v-for="(item,index) in $store.state.man_Clothes" :allinfo_id="item.Id"  @click="send_info(item.Id)">
					<img :src="item.ProductPic"/>
					<div class="ProductName_span">
						<span>{{item.ProductName}}</span>
					</div>
					<div class="ProductName_span2">
						<span>￥{{item.MarketPrice}}</span>
					</div>
					<div class="ProductName_span3">
						<span>￥{{item.ShowPrice}}</span>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				iconfont:'iconfont icon-shangxiajiantou',
				name:'Popularity',
				backid:'',
				listid:"",
				renqi:'人气',
				xiaoliang:'销量',
				New:"最新",
				price:'价格',
				shaixuan:'筛选',
				man_Clothes:"",
				lady_Clothes:'',
				price_con:true
			}
		},
		mounted(){
			//console.log(this.$store.state.count)
			this.listid=this.$route.params.Info_id
			console.log(this.listid)
//			console.log(this.$store.state.count)
			this.loadread()
		},
		methods:{
			send_info(info_id){
				console.log(info_id)
				this.$store.state.infozancun=info_id
				this.$router.push({name:'info',params:{info_id}})
			},
			changs(){
				this.listid=this.$store.state.count
				console.log(this.listid)
				this.loadread()
			},
			changs_xiaoliang(){
				this.listid=92
				console.log(this.listid)
//				this.listid=this.$store.state.count
				this.loadread()
			},
			changs_New(){
				this.listid=93
				//this.$route.params.Info_id=this.listid
				console.log(this.listid)
				this.loadread()
			},
			changs_price(){
				this.price_con=!this.price_con
				console.log(this.price_con)
				this.listid=94
				console.log(this.listid)
				this.loadread()
			},
			changs_shaixuan(){
				this.listid=95
				console.log(this.listid)
				this.loadread()
			},
			loadread(){
				this.$axios({
					method:"get",
					url:"./static/allinfo.json",
					dataType:"json",
					crossDomain:true,
					cache:false,
				}).then(res =>{
					switch(this.listid){
						case 12111:
						console.log('男士服饰')
						console.log(res)
//						this.man_Clothes=res.data.man_Clothes
						this.$store.state.man_Clothes=res.data.man_Clothes
						break;
						case 92:
						//T恤的销售选项
						if(this.$store.state.count==1){
//							this.man_Clothes=res.data.t_xu_Salesvolume
							this.$store.state.man_Clothes=res.data.t_xu_Salesvolume
						}
						//西装的销售选项
						if(this.$store.state.count==2){
//							this.man_Clothes=res.data.xizhuang
							this.$store.state.man_Clothes=res.data.xizhuang
						}
						//毛衣的销售选项
						if(this.$store.state.count==3){
//							this.man_Clothes=res.data.maoyi_nan_Salesvolume
							this.$store.state.man_Clothes=res.data.maoyi_nan_Salesvolume
						}
						//男士服饰的销售选项
						if(this.$store.state.count==12111){
//							this.man_Clothes=res.data.man_Clothes_Salesvolume
							this.$store.state.man_Clothes=res.data.man_Clothes_Salesvolume
						}
						break;
						case 93:
						console.log("最新")
						if(this.$store.state.count==12111){
//							this.man_Clothes=res.data.man_Clothes_New
							this.$store.state.man_Clothes=res.data.man_Clothes_New
//							this.listid=this.$store.state.count
						}
						break;
						case 94:
						console.log("价格")
						if(this.price_con==true){
//							this.man_Clothes=res.data.man_Clothes_New
							this.$store.state.man_Clothes=res.data.man_Clothes_Salesvolume
						}else{
//							this.man_Clothes=res.data.man_Clothes_Salesvolume
							this.$store.state.man_Clothes=res.data.man_Clothes_uppirce
							
						}
						break;
						case 95:
						console.log("筛选")
						break;
						case 1:
						console.log(this.listid)
						console.log(res)
						this.$store.state.man_Clothes=res.data.t_xu
						break;
						case 2:
						console.log(this.listid)
						console.log(res)
						this.$store.state.man_Clothes=res.data.xizhuang
						break;
						case 3:
						console.log(this.listid)
						console.log(res)
						this.$store.state.man_Clothes=res.data.maoyi_nan
						break;
					}
				})
			}
		},
	}
</script>

<style lang="less">
	div{
		background: white;
	}
	span{
		background: white;
	}
	.alllist_nav{
		width: 100%;
		height: 63px;
		background: white;
		margin-top: 81px;
		ul{
			width: 100%;
			height: 63px;
			background: white;
			display: flex;
			justify-content: space-around;
			align-items: center;
			li{
				background: white;
			}
		}
	}
	.allinfo_list{
		width:100%;
		ul{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-around;
			
			li{
				width: 50%;
				height: 535px;
				background: white;
				img{
					width: 350px;
					height: 350px;
				}
				.ProductName_span{
					margin-left: 20px;
				}
				.ProductName_span2{
					margin-left: 20px;
					span{
						text-decoration: line-through;
						color: rgb(145,145,145);
						font-size: 28px;
					}
				}
				.ProductName_span3{
					margin-left: 20px;
					span{
						color: rgb(211,117,32);
						font-size: 28px;
						
					}
				}
			}
		}
	}
</style>


