<script>
	export default {
		name: 'info',
		data() {
			return {
				//轮播图
				isfalse: true,
				ftcolor:{
					background:"black",
				},
				ftcolor2:{
					background:"white",
					color:"black"
				},
				//				istrue:false,
				//				购物车弹出页面数据
				sell_in: '',
				size_colorA: 'bule',
				order_num: 1,
				money: '',
				kucun: '',
				size: '',
				size_list: '',
				imgs: '',
				//				----------
				imglist: '',
				swip: '',
				imglist1: '',
				iconfont: 'iconfont icon-xingji',
				name: '',
				logo_tu: '',
				BrandName: '',
				tutu: '',
				ShowPrice: '',
				AfterService: '',
				ProductComment: '',
				ProductComment_CreateTime: '',
				ProductName: '',
				Account_User: '',
				MarketPrice: '',
				Grade: '',
				Grade_show: true,
				swiperOption: {
					centeredSlides: false,
					pagination: {
						el: '.swiper-pagination',
					},
				},
				swiperOption1: {
					slidesPerView: 'auto',
					centeredSlides: false,
					slidesPerView: 3, //一行显示3个
					slidesPerColumn: 1, //显示2行
					//  				spaceBetween : '15%'
				},
			}
		},
		//过滤器转换时间戳
		filters: {
			formatDate: function(value) {
				let date = new Date(value);
				let y = date.getFullYear();
				let MM = date.getMonth() + 1;
				MM = MM < 10 ? ('0' + MM) : MM;
				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d;
				return y + '-' + MM + '-' + d + ' ';
			}
		},
		methods: {
			qiehuan() {
				this.isfalse = !this.isfalse
			},
			size_li(index, item) {
				console.log(index)
				console.log(item)
				this.kucun = item.Sort
				this.sell_in = item.Name
				//循环每个让他变成flase
				for(var i = 0; i < this.size_list.length; i++) {
					this.size_list[i].size_off = false;
				}
				this.size_list[index].size_off = true;
			},
			order_dw(e) {
				this.order_num--
					if(this.order_num < 1) {
						alert('至少选择一件')
						this.order_num = 1
					}
			},
			order_up(e) {
				this.order_num++
					if(this.order_num > this.kucun) {
						alert('库存不足只有' + this.kucun + '件')
						this.order_num = this.kucun
					}
			},
			back1() {

				this.$store.state.article_show = false
			},
			a() {
				this.$store.state.article_show = true
			},
			goidcar() {
//				this.$store.state.carno.push(this.infoid)
				console.log(this.$store.state.carno)
				console.log(this.sell_in)
				if(this.sell_in == "") {
					alert('请选择尺码')
				} else {
					this.$axios({
						url: "./users/isLogin",
						method: "get",
						params: {
//							user: this.checkin
						}
					}).then(res => {
						console.log(res)
						if(res.data.err == '未登录') {
							alert('请先登录')
							this.$router.push({path: 'login'})
							this.loadlist()
						} else {
							console.log('已经登录')
							this.$axios({
								url: "./viadante/uplist",
								method: "post",
								data: {
									action: 'addd',
									viaid: this.infoid,
									sell_in: this.sell_in,
									order_num: this.order_num,
									money: this.money,
									imgs: this.imgs,
									name: this.name
								}
							}).then(res => {
								console.log(res)
								alert('已添加到购物车，请到购物车查看')
								this.$store.state.article_show = false
							})
						}

					})
				}
			},
			back() {
				console.log(this.$route.path)

				if(this.$route.path == '/info') {
					this.$router.push({
						name: 'home'
					})
				} else if(this.$route.path == '/info/twxq') {
					this.$router.push({
						name: 'home'
					})
				} else if(this.$route.path == "/info/gwyw") {
					this.$router.push({
						name: 'home'
					})
				}
				this.$router.go(-2)
				this.$store.commit("bind", true)
			},
			loadlist() {
				this.$axios({
					method: "get",
					url: "./static/info.json",
					dataType: "json",
					crossDomain: true,
					cache: false,
				}).then(res => {
					console.log(res)
					//在home传数据给详情页的时候把id先存到vuex中
					console.log(this.infoid)
					this.infoid=this.$store.state.infozancun ; 
					console.log(this.$store.state.infozancun)
					for(var i = 0; i < res.data.Info.length; i++) {
						if(this.infoid == res.data.Info[i].Id) {
							console.log(res.data.Info[i]);
							localStorage.setItem('infolist', JSON.stringify(res.data.Info[i]))
							this.size_list = res.data.Info[i].SpecLst[0].svLst
							this.imgs = res.data.Info[i].ProductPic
							this.kucun = res.data.Info[i].SpecLst[0].svLst[0].Sort
							this.money = res.data.Info[i].ShowPrice
							//=======================================  下面是info页面渲染数据，上面是购物车弹出页面渲染    =================================================
							this.name = res.data.Info[i].ProductName
							this.logo_tu = res.data.Info[i].BrandLogoPath
							this.BrandName = res.data.Info[i].BrandName
							this.tutu = res.data.Info[i].productPicList
							this.MarketPrice = res.data.Info[i].MarketPrice
							this.ShowPrice = res.data.Info[i].ShowPrice
							//							this.AfterService=res.data.Info[0].AfterService[0].AfterService_tu1
							this.ProductComment = res.data.Info[i].ProductComment.CommentInfo
							//使用必须把字符串转为数字，不然事件戳不能转换为日期
							this.ProductComment_CreateTime = Number(res.data.Info[i].ProductComment.CreateTime.slice(6, 19))
							this.ProductName = res.data.Info[i].ProductComment.ProductName
							this.Account_User = res.data.Info[i].ProductComment.Account_User
							this.Grade = res.data.Info[i].ProductComment.Grade
							this.imglist = res.data.Info[i].sell_tu
							this.imglist1 = res.data.Info[i].AfterService
							this.swip = res.data.Info[i].SimilarProductList
							//							console.log(this.Grade)
						}
					}
					//新增size_list的数据，每一个默认为false
					for(var i = 0; i < this.size_list.length; i++) {
						this.size_list[i].size_off = false;
					}
				})
			}
		},
		mounted() {

			this.infoid = this.$route.params.info_id
			console.log(this.infoid)
			this.loadlist()

			this.$store.commit("bind", false)
		}
	}
</script>

