					span {
						margin-left: 20px;
						font-size: 40px;
					}
					ul {
						margin-top: 10px;
						display: flex;
						li {
							border-radius: 10px;
							display: flex;
							span {
								background: white;
								width: 57px;
								height: 57px;
								text-align: center;
								line-height: 57px;
								border-radius: 15px;
								font-size: 25px;
								color: white;
								border: 1px solid black;
								color: black;
							}
						}
					}
				}
			}
			.art_shul {
				margin-top: 30px;
				display: flex;
				justify-content: space-between;
				span:nth-child(1) {
					margin-left: 20px;
					font-size: 30px;
				}
				;
				div {
					margin-right: 20px;
					button {
						border: none;
						outline-color: bisque;
					}
				}
			}
			#send_car {
				width: 100%;
				height: 90px;
				background: rgb(241, 130, 0);
				position: absolute;
				bottom: 0;
				display: flex;
				justify-content: center;
				align-items: center;
				span {
					font-size: 30px;
					background: rgb(241, 130, 0);
				}
			}
		}
	}
	/*相似商品*/
	
	#twxq {
		width: 100%;
		/*background: yellow;*/
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}
	
	img {
		width: 94%;
		background: red;
	}
	
	.swip_box {
		/*margin-left: 40px;*/
		margin-top: 20px;
		width: 100%;
		height: 400px;
		/*margin-bottom: 20px;*/
		div{
			width: 100%;
			height: 400px;
			margin-bottom: 100px;
			z-index: 0;
			.swippp {
				// height: 300px;
				width: 182px;
			img {
				width: 182px;
				height: 182px;
				}
			.sw1 {
				width: 100%;
				// height: 60px;
				// word-break: break-all;
				word-break:break-all;
				display:-webkit-box;
				-webkit-line-clamp:3;
				-webkit-box-orient:vertical;
				overflow:hidden;
				}
			.sw2 {
				margin: 0;
				color: red;
				margin-top: 15px;
				}
			}
		}
		
	}
</style>


