<template>
	<div id="inlogin">
		<div id="back" @click="back">
			<i>关</i>
		</div>
		<div id="denglu" v-if="qiehuan">
			<div class="loginbox">
				<img src="../assets/111111.jpg"/>
			</div>
			<div class="loginbox1">
				<input type="text" name="" id="zhanghao1" v-model="userName" placeholder="手机号码" />
			</div>
			<div class="loginbox2">
				<input type="password" name="" id="password1" v-model="password" placeholder="密码" />
			</div>
			<div class="loginbox3">
				<input id="loginp1" @click="login" type="button" value="登录"  />
			</div>
			<div class="loginbox4">
				<span id="zec" @click="zc">立即注册</span>
				<span>忘记密码</span>
			</div>
		</div>
		<div id="zhuce" v-if="qiehuan==false">
			<div class="loginbox">
				<img src="../assets/111111.jpg"/>
			</div>
			<div class="loginbox1">
				<input type="text" name="" id="zhanghao" v-model="userName" placeholder="手机号码" />
			</div>
			<div class="loginbox2">
				<input type="password" name="" id="password" v-model="password" placeholder="密码" />
			</div>
			<div class="loginbox33">
				<input id="loginp" @click="zhuche" type="button" value="注册"  />
			</div>
			<div class="loginbox4">
				<span @click="zc">已有账号立即登录</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'login',
		data(){
			return{
				qiehuan:true,
				userName:"",
				password:"",
				veri:"",//发送的验证码
				getVeri:"",//获取到的验证码
				loginname:'',
				checkin:'',
			}
		},
		methods:{
			zhuche(){
				if(this.userName==''){
					alert('手机号码不可以为空')
				}else if(!(/^1[34578]\d{9}$/.test(this.userName))){
					alert('手机号码格式错误')
				}else if(!(/^[a-zA-Z0-9_-]{4,16}$/.test(this.password))){
					alert('密码格式4到16位（字母，数字，下划线，减号）')
				}else{
					this.$axios({
					url:"./users/inmin",
					method:"post",
					data:{
						userName:this.userName,
						password:this.password
						}
					}).then(res=>{
						console.log(res)
						if(res.data.err=="用户名已被注册"){
							alert("用户名已被注册")
						}else if(res.data.success=="注册成功"){
							alert("注册成功")
							this.$store.state.cheackin=true
						}else if(res.data.success=="注册失败"){
							alert("注册失败")
						}
					})
				}	
			},
			zc(){
				this.qiehuan=!this.qiehuan
//				var dc=document.getElementById("zhanghao1");
//				console.log(zcs)
//				dc.value=''
//				this.userName=''
//				this.password=''
			},
			back(){
				this.$router.go(-2);
				this.$store.state.sss=true
			},
			cheackin(){
				//是否登录接口
				this.$axios({
					url:"./users/isLogin",
					method:"get",
					params:{
						user:this.checkin
					}
				}).then(res=>{
					console.log(res)
					if(res.data.err=='未登录'){
						this.$store.state.cheackin=true
					}else{
						this.$store.state.cheackin=false
					}
					
				}).catch(err=>{
					
				})
				
			},
			login(){
					
					//登录方法
					this.$axios({
							method:"post",
							url:"/users/login",
							data:{
								userName:this.userName,
								password:this.password
							}
						}).then(res=>{
//							console.log(res.data.err)
							if(res.data.err=='用户不存在'){
								alert('用户不存在')
							}
							if(res.data.err=='密码错误'){
								alert('密码错误')
							}
							this.loginname=res.data[0].userName
//								console.log(this.loginname)
								this.$router.go(-1);
								this.$store.state.cheackin=true
								console.log(this.$store.state.cheackin)
						}).catch(err=>{
							
						})
			},
		},
		mounted(){
		}
	}
</script>

<style lang="less" scoped="scoped" >
	*{
		margin: 0;
		padding: 0;
		input::-webkit-input-placeholder{  
		    color: rgb(255,255,255);
		}
	}
	#inlogin{
		width:100%;
		height: 100%;
		background: rgb(152,162,172);
		z-index: 10000;
		position: fixed;
		top:0;
		/*bottom: 0;*/
		#back{
			width: 100%;
			height: 90px;
			display: flex;
			align-items: center;
			background: transparent;
			justify-content: flex-end;
			i{
				margin-right: 30px;
				width: 50px;
				height: 50px;
			}
		}
		.loginbox{
			background: transparent;
			width: 100%;
			height: 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-top: 100px;
			margin-bottom: 50px;
			img{
				
				width: 220px;
				height: 50px;
			}
		}
		.loginbox1{
			background: transparent;
			width: 100%;
			height: 90px;
			display: flex;
			justify-content: center;
			align-items: center;
			input{
				border: 2px solid transparent;
				outline: 0;
				/*outline-color:rgb(231,235,244);*/
				border-radius: 15px;
				background: rgb(172,180,183);
				width: 650px;
				height: 90px;
				padding-left: 40px;
				box-sizing: border-box;
			}
		}
		.loginbox2{
			background: transparent;
			margin-top: 20px;
			width: 100%;
			height: 90px;
			display: flex;
			justify-content: center;
			align-items: center;
			input{
				border: 2px solid transparent;
				outline: 0;
				border-radius: 15px;
				background: rgb(172,180,183);
				width: 650px;
				height: 90px;
				padding-left: 40px;
				box-sizing: border-box;
			}
		}
		.loginbox3{
			background: transparent;
			margin-top: 20px;
			width: 100%;
			height: 90px;
			display: flex;
			justify-content: center;
			align-items: center;
			input{
				border: 1px solid rgb(231,235,244);
				outline: 0;
				border-radius: 15px;
				background: white;
				width: 650px;
				height: 90px;
				box-sizing: border-box;
				font-size: 40px;
			}
		}
		.loginbox33{
			background: transparent;
			margin-top: 20px;
			width: 100%;
			height: 90px;
			display: flex;
			justify-content: center;
			align-items: center;
			input{
				border: 1px solid rgb(231,235,244);
				outline: 0;
				border-radius: 15px;
				background: white;
				width: 650px;
				height: 90px;
				box-sizing: border-box;
				font-size: 40px;
			}
		}
		.loginbox4{
			background: transparent;
			margin-top: 20px;
			width: 100%;
			height: 90px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			span{
				background:  rgb(152,162,172);
				color: rgb(255,255,255);
				&:nth-child(1){
					margin-left: 50px;
				}
				&:nth-child(2){
					margin-right: 50px;
				}
			}
		}
	}
	#denglu{
		background: rgb(152,162,172);
	}
	#zhuce{
		background: rgb(152,162,172);
	}
</style>


