<template>
	<div>
		<div class="top_head">
			<i class="iconfont icon-sousuo" style="text-align: center;background: transparent;font-size: 20px;"></i>
			<img src="../assets/1548341655.jpg"/>
			<i class="iconfont icon-shouhoukefu" style="text-align: center;background: transparent;font-size: 20px;"></i>
		</div>
		<div class="fenlei_list">
			<ul>
				<li><router-link to="/fenlei/list"><span>分类</span></router-link></li>
				<li><router-link to="/fenlei/pinpai"><span>品牌</span></router-link></li>
			</ul>
		</div>
		<router-view />
	</div>
</template>

<script>
	export default{
		name:'fenlei',
		data(){
			return{
				dature:true,
			}
		},
		methods:{
			
			
		},
		mounted(){
			this.$store.commit("bind",true)
			this.$store.state.cheackin=false	
		}
	}
</script>

<style lang="less">
	
	*{
		margin: 0;
		padding: 0;
	}
	.top_head{
		border-bottom: 1px solid #000000;
		/*position: fixed;
		top: 0;*/
		background: white;
		z-index: 999;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 80px;
		/*background: red;*/
		i{
			width: 45px;
			height: 42px;
			background: yellow;
			margin: 0 20px 0 20px;
		}
		img{
			width:147px;
			height: 30px;
		}
	}	
	.fenlei_list{
		width: 100%;
		height: 63px;
		margin-top: 80px;
		/*background: yellow;*/
		border-top: 0.5px #000000 solid;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		text-decoration: none;
		ul{
			width: 100%;
			height: 63px;
			display: flex;
			align-items: flex-start;
			justify-content: center;
			li{
				width: 50%;
				height: 63px;
				display: flex;
				justify-content:center;
				align-items: center;
				span{
					font-size: 30px;
					text-decoration: none;
				}
			}
		}
	}
	a{
		text-decoration: none;
	}
	.router-link-active{
		text-decoration: none;
	}
</style>


