<template>
	<div>
		<div class="message_head">
			<i style="background: transparent;"></i>
			<span>主播间</span>
			<i>
				<i class="iconfont icon-sousuo"></i>
				<i class="iconfont icon-shouhoukefu"></i>
			</i>
		</div>
		<div class="message_two">
			<div class="message_thr">
				<img src="../assets/121.jpg"/>
			</div>
		</div>
		<div class="anchor">
			<div class="top_anchor">
					<span>——</span>
					<span>top主播</span>
					<span>——</span>
			</div>
			<div class="ranking_anchor">
				<ul>
					<li v-for="(item,index) in ranking_anchor" :key='index'>
						<img :src="item.anchor1"/>
						<div class="ranking"><i :class="item.tubiao"></i></div>
						<span>{{item.name}}</span>
						<button @click="follow(index)" v-if="item.follow_on">{{item.follow}}</button>
						<button @click="follow(index)" v-if="item.follow_on==false" :pid="item.id">{{item.follow_in}}</button>
					</li>
				</ul>
			</div>
			<div class="tabnav">
				<ul>
					
					<li>
						<router-link to="/message/quanzi" style='text-decoration: none;color: #000000;'>
							<span @click="ononon()"  :style="on?[sty]:''">但丁圈</span>
						</router-link>
					</li>
					<li>
						<router-link to="/message/zhuboquan" style='text-decoration: none;color: #000000;'>
							<span @click="ononon()" :style="on==false?[sty]:''">主播圈</span>
						</router-link>
						
					</li>
				</ul>
				
			</div>
			<router-view />
			
		</div>
	</div>
</template>

<script>
	export default{
		name:"message",
		data(){
			return{
				transitionName:"",
				ranking_anchor:'',
				sty:{
					borderBottom:"1px solid green"
				},
				on:'',
				checkin:'',
			}
		},
		methods:{
			cheackin(){
				//是否登录接口
				this.$axios({
					url:"./users/isLogin",
					method:"get",
					params:{
						user:this.checkin
					}
				}).then(res=>{
					console.log(res)
					if(res.data.err=='未登录'){
						this.$router.push({path:'/login'})
					}
					
				}).catch(err=>{
					
				})
				
			},
			ononon(){
				this.on=!this.on
			},
			follow(index){
				this.ranking_anchor[index].follow_on=!this.ranking_anchor[index].follow_on
				console.log(this.ranking_anchor[index].follow_on)
				console.log(index)
			},
			loder(){
				this.$axios({
					method:'get',
					url:"./static/showinfo.json",
					dataType:"json",
					crossDomain: true,
					cache: false
				}).then(res=>{
					console.log(res)
					this.ranking_anchor=res.data.off_white[0].anchor
					console.log(this.ranking_anchor)
				})
			}
		},
		mounted(){
			
			this.cheackin()
			this.loder()
			this.on=this.$store.state.dandingquan_on
			console.log(this.$store.state.cheackin)
//			console.log(this.on)
		}
	}
</script>

<style lang="less">
	*{
		margin: 0;
		padding: 0;
		background: rgb(245,245,245);
	}
	/*span{
		background: white;
	}*/
	div{
		background: white;
	}
	.message_head{
		background: white;
		position: fixed;
		top: 0;
		z-index: 999;
		width: 100%;
		height: 80px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		span{
			background: white;
			font-size: 35px;
			font-weight: 700;
		}
		i{
			width: 100px;
			height: 42px;
			background: transparent;
			margin-right: 20px;
			display: flex;
			align-items: center;
			justify-content: center;
			i{
				font-size: 36px;
				text-align: center;
			}
		}
	}
	.message_two{
		width: 100%;
		height: 350px;
		margin-top: 80px;
		background: rgb(170,170,170);
		display: flex;
		justify-content: center;
		.message_thr{
			width: 557px;
			height: 100%;
			background: rgb(242,242,242);
			img{
				width: 557px;
				height: 100%;
			}
		}
	}
	.anchor{
		width: 100%;
		height: 400px;
		background: white;
		margin-top: 20px;
		.top_anchor{
			background: transparent;
			width: 100%;
			height: 85px;
			/*background: yellow;*/
			display: flex;
			justify-content: center;
			align-items: center;
			span{
				background: transparent;
				font-size: 35px;
			}
			span:nth-child(1){
				background: transparent;
				margin-right: 5px;
				color: pink;
			}
			span:nth-child(3){
				margin-left: 5px;
				color: pink;
			}
		}
		.ranking_anchor{
			width: 100%;
			height: 295px;
			background: transparent;
			ul{
				width: 100%;
				background: transparent;
				height: 295px;
				display: flex;
				justify-content: space-around;
				
				li{
					width: 135px;
					height: 295px;
					background: white;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-wrap: wrap;
					img{
						width: 130px;
						height: 130px;
						border-radius: 65px;
					}
					.ranking{
						background: white;
						width: 100%;
						text-align: center;
					}
					span{
						background: white;
						width: 100%;
						text-align: center;
					}
					button{
						width: 90px;
						border: 1px solid saddlebrown;
						border-radius: 10px;
					}
				}
			}
		}
	}
	.tabnav{
		border-bottom: 1px solid #969896;
		width: 100%;
		height: 62px;
		background: white;
		margin-top: 30px;
		ul{
			width: 100%;
			height: 61px;
			background:white;
			display: flex;
			justify-content: space-around;
			align-items: center;
			li{
				width: 50%;
				height: 61px;
				background:white;
				display: flex;
				justify-content: space-around;
				align-items: center;
				span{
					padding-bottom: 5px;
				}
			}
		}
	}
</style>


